.booking {
	width: 100%;
	margin: 3rem 0;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	height: 100%;
}

.booking .left {
	text-align: center;
	margin: auto;
	padding: 1rem 4rem;
	max-width: 600px;
}

.booking .left p {
	margin: 1.2rem 0;
}

.booking .right {
	width: 100%;
}

.right .img-container {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
	position: relative;
	align-items: center;
	text-align: center;
}

.right .img {
	max-width: 60%;
	border: 1px solid #333;
}

.right .top {
	grid-column: 1 / span 8;
	grid-row: 1;
	padding-top: 20%;
	z-index: 6;
}

.right .bottom {
	grid-column: 4/ -1;
	grid-row: 1;
	z-index: 5;
}

@media screen and (max-width: 1140px) {
	.booking {
		grid-template-columns: 1fr;
	}

	.booking .img {
		max-width: 80%;
	}
}
